.navContainer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: wheat;
  margin: 0 auto;
}

  
@media (min-width:769px) {
  .nav{
    backdrop-filter: blur(20px);
  }
  .navScroll{
    background: -webkit-gradient(linear, left top, right top, from(#100C09), color-stop(#342B1E), to(#1F1912));
    background: -o-linear-gradient(left, #100C09, #342B1E, #1F1912);
    background: linear-gradient(0.25turn, #100C09, #342B1E, #1F1912);
    position: fixed;
    left: 0;
    right: 0;
  }
}
  @media (max-width:768.98px) {
    .nav{
      position: fixed;
      left: 0;
    right: 0;
    min-height: 100%;
    }
.navContainer{
  width: 100%;
  position: fixed;
  height: 50px;
  justify-content: space-between;
  background-color: #342B1E;
  z-index: 1;
}
.navContainer::after{
  content: "";
  position: absolute;
  width: 100%;
  height: 50px;
  background-color: #5a493c;
  z-index: 300;
}
  }