/* ================================================= */
*,
*::before,
*::after{
    padding: 0;
    margin: 0;
    border: 0;

    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
a{
    text-decoration: none;
}
ul,
ol,
li{
    list-style: none;
}
img{
    vertical-align: top;
}
h1,h2,h3,h4,h5.h6{
    font-weight: inherit;
    font-size: inherit;
}
html,
body{
    height: 100%;
    line-height: 1;
    font-size: 62.5%;
    font-family: Arial,"Helvetica Neue", Helvetica, sans-serif;
    background-color: #F8F8F8;
}
._lock{
    overflow: hidden;
}
.wrapper{
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;

}
.wrapper._loaded{
}
.page{
    padding: 90px 0px 0px 0px;
    flex: 1 1 auto;
}
.active{
  color:red;
  background-color: red;
}
